___CSS_LOADER_EXPORT___.push([module.id, `#todo-display-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 12px;
    padding-top: 32px;
}

.todoContainer {
    height: min-content;
    background-color: var(--slate-200);
    border-radius: 8px;
    margin: 8px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
                rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}

.todoContainer:hover {
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,
                rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}

.todoContainer.complete .todoTitle,
.todoContainer.complete .todoInfo,
.todoContainer.complete .todoDescription,
.todoContainer.complete .todoChecklist {
    filter: contrast(10%) grayscale(10%) brightness(10%) saturate(10%) opacity(20%);
}

.priorityLow, .priorityNormal, .priorityHigh,
.priorityUrgent, .priorityCritical {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 3px;
    padding: 16px;
    gap: 8px;
    min-width: 250px;
    width: 25vw;
    max-width: 300px;
    border-radius: 4px;
}

.priorityLow { box-shadow: rgba(37, 99, 235, 0.6) 0px 0px 0px 3px; }
.priorityNormal { box-shadow: rgba(22, 163, 74, 0.6) 0px 0px 0px 3px; }
.priorityHigh { box-shadow: rgba(202, 138, 4, 0.6) 0px 0px 0px 3px; }
.priorityUrgent { box-shadow: rgba(234, 88, 12, 0.6) 0px 0px 0px 3px; }
.priorityCritical { box-shadow: rgba(220, 38, 38, 0.6) 0px 0px 0px 3px; }

.priorityLow .todoPriority { color: rgba(37, 99, 235, 0.7); }
.priorityLow .todoTitle { border-bottom: 1px solid rgba(37, 99, 235, 0.4); }

.priorityNormal .todoPriority { color: rgba(22, 163, 74, 0.7); }
.priorityNormal .todoTitle { border-bottom: 1px solid rgba(22, 163, 74, 0.4); }

.priorityHigh .todoPriority { color: rgba(202, 138, 4, 0.7); }
.priorityHigh .todoTitle { border-bottom: 1px solid rgba(202, 138, 4, 0.4); }

.priorityUrgent .todoPriority { color: rgba(234, 88, 12, 0.7); }
.priorityUrgent .todoTitle { border-bottom: 1px solid rgba(234, 88, 12, 0.7); }

.priorityCritical .todoPriority { color: rgba(220, 38, 38, 0.6); }
.priorityCritical .todoTitle { border-bottom: 1px solid rgba(220, 38, 38, 0.7);}

.todoTitle {
    text-align: center;
    font-family: "Roboto-500";
    font-size: 1.6rem;
    width: 100%;
    padding-bottom: 4px;
    margin-bottom: 16px;
    color: var(--slate-800);
    border-bottom: 1px solid var(--slate-500);
    overflow-wrap: break-word;

}

.todoInfo {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
}

.todoDueDate {
    font-family: "Roboto-500";
    color: var(--slate-700);
}

.todoDescription {
    font-family: "Roboto-400";
    font-size: 1rem;
    color: var(--slate-700);
    margin-bottom: 16px;
    overflow-wrap: break-word;
}

.todoChecklist {
    font-family: "Roboto-400";
    display: flex;
    flex-direction: column;
    align-items: start;
    margin-bottom: 16px;
    word-break: break-word;
}

.todoChecklist > div {
    display: flex;
    width: 100%;
    padding-bottom: 4px;
}

.todoChecklist input[type="checkbox"] {
    width: min-content;
    margin: 0;
    margin-right: 8px;
}

.checked label {
    text-decoration: line-through;
}

.todoActions {
    display: flex;
    justify-content: space-between;
}
.todoActions button.toggleComplete {
    min-width: 105px;
}

@media screen and (max-width: 619px) {
    #todo-display-container {
        padding-top: 8px;
    }
 }`, "",{"version":3,"sources":["webpack://./src/styles/todoDisplay.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,uBAAuB;IACvB,eAAe;IACf,SAAS;IACT,iBAAiB;AACrB;;AAEA;IACI,mBAAmB;IACnB,kCAAkC;IAClC,kBAAkB;IAClB,WAAW;IACX;sDACkD;AACtD;;AAEA;IACI;uDACmD;AACvD;;AAEA;;;;IAII,+EAA+E;AACnF;;AAEA;;IAEI,aAAa;IACb,sBAAsB;IACtB,8BAA8B;IAC9B,WAAW;IACX,aAAa;IACb,QAAQ;IACR,gBAAgB;IAChB,WAAW;IACX,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA,eAAe,kDAAkD,EAAE;AACnE,kBAAkB,kDAAkD,EAAE;AACtE,gBAAgB,kDAAkD,EAAE;AACpE,kBAAkB,kDAAkD,EAAE;AACtE,oBAAoB,kDAAkD,EAAE;;AAExE,6BAA6B,6BAA6B,EAAE;AAC5D,0BAA0B,+CAA+C,EAAE;;AAE3E,gCAAgC,6BAA6B,EAAE;AAC/D,6BAA6B,+CAA+C,EAAE;;AAE9E,8BAA8B,6BAA6B,EAAE;AAC7D,2BAA2B,+CAA+C,EAAE;;AAE5E,gCAAgC,6BAA6B,EAAE;AAC/D,6BAA6B,+CAA+C,EAAE;;AAE9E,kCAAkC,6BAA6B,EAAE;AACjE,+BAA+B,+CAA+C,CAAC;;AAE/E;IACI,kBAAkB;IAClB,yBAAyB;IACzB,iBAAiB;IACjB,WAAW;IACX,mBAAmB;IACnB,mBAAmB;IACnB,uBAAuB;IACvB,yCAAyC;IACzC,yBAAyB;;AAE7B;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,yBAAyB;IACzB,uBAAuB;AAC3B;;AAEA;IACI,yBAAyB;IACzB,eAAe;IACf,uBAAuB;IACvB,mBAAmB;IACnB,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,mBAAmB;IACnB,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,iBAAiB;AACrB;;AAEA;IACI,6BAA6B;AACjC;;AAEA;IACI,aAAa;IACb,8BAA8B;AAClC;AACA;IACI,gBAAgB;AACpB;;AAEA;IACI;QACI,gBAAgB;IACpB;CACH","sourcesContent":["#todo-display-container {\r\n    display: flex;\r\n    justify-content: center;\r\n    flex-wrap: wrap;\r\n    gap: 12px;\r\n    padding-top: 32px;\r\n}\r\n\r\n.todoContainer {\r\n    height: min-content;\r\n    background-color: var(--slate-200);\r\n    border-radius: 8px;\r\n    margin: 8px;\r\n    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,\r\n                rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;\r\n}\r\n\r\n.todoContainer:hover {\r\n    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,\r\n                rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;\r\n}\r\n\r\n.todoContainer.complete .todoTitle