___CSS_LOADER_EXPORT___.push([module.id, `html {
    min-height: 100%;
    position: relative;
}

body {
    margin: 0;
    padding: 0;
    font-family: "Roboto-500";
    color: var(--slate-900);
    background-color: var(--secondary-background);
}

button {
    font-family: "Roboto-300";
    appearance: button;
    background-color: var(--blue-600);
    border: 1px solid var(--blue-600);
    border-radius: 4px;
    box-sizing: border-box;
    color: #FFFFFF;
    cursor: pointer;
    line-height: 1.15;
    overflow: visible;
    padding: 8px 12px;
    position: relative;
    text-align: center;
    text-transform: none;
    transition: all 80ms ease-in-out;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    width: max-content;
}

button:focus {
    outline: 0;
}

button:hover {
    background-color: #0A46E4;
    border-color: #0A46E4;
}

button:active {
    background-color: #0039D7;
    border-color: #0039D7;
}

button.removeButton {
    background-color: var(--red-600);
    border-color:var(--red-600);
}

button.removeButton:hover {
    background-color: #ce0e0e;
    border-color: #ce0e0e;
}

button.removeButton:active {
    background-color: #c50e0e;
    border-color: #c50e0e;
}

h1, h2 {
    margin: 0
}

input {
    /* padding: 12px 16px; */
    font-size: 1.2rem;
    width: 70%;
}

input[type="date"] {
    width: 40%;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

#main-content-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 70px;
}`, "",{"version":3,"sources":["webpack://./src/styles/index.css"],"names":[],"mappings":"AAAA;IACI,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,SAAS;IACT,UAAU;IACV,yBAAyB;IACzB,uBAAuB;IACvB,6CAA6C;AACjD;;AAEA;IACI,yBAAyB;IACzB,kBAAkB;IAClB,iCAAiC;IACjC,iCAAiC;IACjC,kBAAkB;IAClB,sBAAsB;IACtB,cAAc;IACd,eAAe;IACf,iBAAiB;IACjB,iBAAiB;IACjB,iBAAiB;IACjB,kBAAkB;IAClB,kBAAkB;IAClB,oBAAoB;IACpB,gCAAgC;IAChC,iBAAiB;IACjB,yBAAyB;IACzB,0BAA0B;IAC1B,kBAAkB;AACtB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,yBAAyB;IACzB,qBAAqB;AACzB;;AAEA;IACI,yBAAyB;IACzB,qBAAqB;AACzB;;AAEA;IACI,gCAAgC;IAChC,2BAA2B;AAC/B;;AAEA;IACI,yBAAyB;IACzB,qBAAqB;AACzB;;AAEA;IACI,yBAAyB;IACzB,qBAAqB;AACzB;;AAEA;IACI;AACJ;;AAEA;IACI,wBAAwB;IACxB,iBAAiB;IACjB,UAAU;AACd;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,gBAAgB;IAChB,UAAU;IACV,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,mBAAmB;AACvB","sourcesContent":["html {\r\n    min-height: 100%;\r\n    position: relative;\r\n}\r\n\r\nbody {\r\n    margin: 0;\r\n    padding: 0;\r\n    font-family: \"Roboto-500\";\r\n    color: var(--slate-900);\r\n    background-color: var(--secondary-background);\r\n}\r\n\r\nbutton {\r\n    font-family: \"Roboto-300\";\r\n    appearance: button;\r\n    background-color: var(--blue-600);\r\n    border: 1px solid var(--blue-600);\r\n    border-radius: 4px;\r\n    box-sizing: border-box;\r\n    color: #FFFFFF;\r\n    cursor: pointer;\r\n    line-height: 1.15;\r\n    overflow: visible;\r\n    padding: 8px 12px;\r\n    position: relative;\r\n    text-align: center;\r\n    text-transform: none;\r\n    transition: all 80ms ease-in-out;\r\n    user-select: none;\r\n    -webkit-user-select: none;\r\n    touch-action: manipulation;\r\n    width: max-content;\r\n}\r\n\r\nbutton:focus {\r\n    outline: 0;\r\n}\r\n\r\nbutton:hover {\r\n    background-color: #0A46E4;\r\n    border-color: #0A46E4;\r\n}\r\n\r\nbutton:active {\r\n    background-color: #0039D7;\r\n    border-color: #0039D7;\r\n}\r\n\r\nbutton.removeButton {\r\n    background-color: var(--red-600);\